import React, { Component } from "react";
import Measure from "react-measure";
import { extent, max } from "d3-array";
import { scaleLinear } from "d3-scale";
import { line, curveLinear as curve } from "d3-shape";

import "./players-dataviz.css";

const WIDTH = 1200;
const HEIGHT = 600;

const margins = {
  left: 100,
  top: 20,
  bottom: 40,
  right: 20
};

// const selectedPlayers = ["Helmut Haller"];//'Michel Platini', 'Zlatan Ibrahimovic', 'Alessandro Del Piero', 'Federico Giampaolo'];

class PlayersDataViz extends Component {
  constructor(props) {
    super(props);
    this.state = {
      players: [],
      selectedPlayers: [],
      dimensions: {
        width: 0,
        height: 0
      }
    };
  }
  async componentDidMount() {
    const { selectedPlayers } = this.state;
    const res = await fetch(
      //`${process.env.PUBLIC_URL}/data/${this.props.file}`
      `//assets/essays/transfers/data/${this.props.file}`
    );
    const players = await res.json();

    let years = [];
    let teamsPlayed = [];
    const parsedPlayers = players
      .filter(d => {
        return 1;
        return selectedPlayers.indexOf(d.name) > -1;
      })
      .map((player, i) => {
        if (selectedPlayers.indexOf(player.key) > -1) {
          console.log("player", player);
        }
        let teams = 0;
        let prev_teamObj = null;
        let prev_country = null;
        let prev_joined = null;
        years = [...years, ...player.value.map(d => d.year)];
        player.value = player.value.map(d => {
          if (!prev_joined) {
            prev_joined = d.joined;
          }
          if (!prev_teamObj || prev_teamObj.team !== d.team) { // d.team !== d.prev_team) {
            teams += 1;
          }
          if(prev_teamObj) {
            d.prev_team = prev_teamObj.team;
          }
          d.teams = teams;
          d.prev_country = prev_country;
          prev_joined = d.joined;
          prev_country = d.country;
          prev_teamObj = d;
          return d;
        });
        teamsPlayed = [...teamsPlayed, teams];
        player.key = `${player.key}`;
        player.teamsPlayed = teams;
        player.yearExtent = extent(player.value, d => d.year);
        player.totalYears = player.yearExtent[1] - player.yearExtent[0];
        return player;
      });

    this.setState({
      players: parsedPlayers.filter(d => d.totalYears > 1) || [], //.filter(d => d.key.search(/Isaksson/gi) > -1) || [],
      years,
      teamsPlayedExtent: extent(teamsPlayed),
      yearsExtent: extent(years),
      playersNames: parsedPlayers
      .filter(d => d.totalYears > 1)
      .map(d => {
        const fullName = d.name.split(" ");
        const name = fullName.shift();
        return {
          name: d.name,
          surnameName: [...fullName, name].join(" "),
          years: d.yearExtent.join("-"),
          key: d.key,
        };
      }).sort((a,b) => {
        if(a.surnameName < b.surnameName) return -1;
        if(a.surnameName > b.surnameName) return 1;
        return 0;
      })
    });
  }
  selectPlayer(key) {
    console.log(key);
    this.setState({
      selectedPlayers: [key]
    });
  }
  render() {
    const {
      players,
      yearsExtent,
      teamsPlayedExtent,
      dimensions,
      selectedPlayers,
      playersNames,
    } = this.state;
    const { width, height } = dimensions;
    console.log(this.state);

    if (!players.length) {
      return null;
    }

    console.log(players);
    console.log(yearsExtent);
    console.log(teamsPlayedExtent);

    const xScale = scaleLinear()
      .domain(yearsExtent)
      .range([0, width - (margins.left + margins.right)]);
    const yScale = scaleLinear()
      .domain([1, teamsPlayedExtent[1]])
      .range([height - (margins.top + margins.bottom), 0]);

    const drawLine = line()
      .x(d => d.x)
      .y(d => d.y)
      .curve(curve);

    const xTicks = xScale.ticks().map(d => {
      const x = xScale(d);
      return (
        <g key={d} className="tick" transform={`translate(${x},0)`}>
          <line x1={0} y1={5} x2={0} y2={15} />
          <text dy={25}>{d}</text>
        </g>
      );
    });

    const drawPlayer = (player, selected) => {
      return (
        <g
          key={`player-${player.key}`}
          rel={`${player.key}: ${player.teamsPlayed} / ${player.totalYears}`}
          className={`player ${selected ? "selected" : ""}`}
        >
          <path
            d={drawLine(
              player.value.map(d => ({ x: xScale(d.year), y: yScale(d.teams) }))
            )}
          />
          {/* {player.value.filter(d => d.country !== d.prev_country && d.prev_country).map(d => { */}
          {player.value.map(d => {
            const x = xScale(d.year);
            const y = yScale(d.teams);
            return (
              <g
                key={`${player.key}-${d.year}-${d.team}`}
                rel={d.team}
                transform={`translate(${x},${y})`}
              >
                {d.team !== d.prev_team &&
                  selected && (
                    <text x="0" y="0" dx={-10} dy="0.25em">
                      {d.team}
                    </text>
                  )}
                {!d.missingYear && (
                  <circle cx={0} cy={0} r={selected ? 4 : 1} />
                )}
                {(d.missingYear && !d.skippedYear) && (
                  <line x1={-2} y1={0} x2={2} y2={0} className="missing-year" />
                )}
              </g>
            );
          })}
        </g>
      );
    };
    console.log('SELECTED PLAYERS', players.filter(player => selectedPlayers.indexOf(player.key) > -1))
    return (
      <div className="dataviz-container">
        <div className="player-list">
          <ul>
            {playersNames.map(d => (
              <li
                className={`${selectedPlayers.indexOf(d.key)>-1?'selected':''}`}
                key={`playername-${d.key}`}
                onClick={e => this.selectPlayer(d.key)}
              >
                {d.surnameName}-{d.years}
              </li>
            ))}
          </ul>
        </div>
        <Measure
          bounds
          onResize={contentRect => {
            this.setState({ dimensions: contentRect.bounds });
          }}
        >
          {({ measureRef }) => (
            <div ref={measureRef} className="players-dataviz">
              <svg width={width} height={height}>
                {width > 0 && (
                  <g
                    className="players"
                    transform={`translate(${margins.left},${margins.top})`}
                  >
                    {players
                      .map(player => drawPlayer(player))}
                  </g>
                )}
                {width > 0 && (
                  <g
                    className="selected-players"
                    transform={`translate(${margins.left},${margins.top})`}
                  >
                    {players
                      .filter(player => selectedPlayers.indexOf(player.key) > -1)
                      .sort((a, b) => {
                        return b.teamsPlayed - a.teamsPlayed;
                      })
                      .map(player => drawPlayer(player, true))}
                  </g>
                )}
                <g
                  className="x-axis"
                  transform={`translate(${margins.left},${height -
                    margins.bottom})`}
                >
                  {xTicks}
                </g>
              </svg>
            </div>
          )}
        </Measure>
      </div>
    );
  }
}

export default PlayersDataViz;



